import React from 'react';
import {Modal, Row, Col, Divider, Form, Input, notification,message} from 'antd';
import {addAOrder,downloadWhitePaper} from "../../api/Businessman";
const { TextArea } = Input;
const FormItem = Form.Item;
let hardWaretotalPrice=0;
const  equipmentName =[
    {id:"gpuServer", name:'GPU服务器',key:0,info:{name:"name",number:"gupServerNumber",Capacity:"gpuNumber",whitePaperUrl:"whitePaperUrl",price:"gpuServerPrice"}},
    {id:"cpuServer", name:'CPU服务器',key:2,info:{name:"name",number:"cpuServerNumber",whitePaperUrl:"whitePaperUrl",price:"cpuServerPrice"}},
    {id:"storageServer", name:'存储服务器',key:1,info:{name:"name",number:"storageServerNumber",Size:"storageSize",hardDiskNumber:"hardDiskNumber",whitePaperUrl:"whitePaperUrl",price:"storageServerPrice"}},
    {id:"ibServer", name:'IB交换机',key:3,info:{name:"name",number:"ibNumber",whitePaperUrl:"whitePaperUrl",price:"ibPrice"}},
    {id:"hardDiskEntity", name:'硬盘',key:4,info:{name:"name",number:"hardDiskNumber",whitePaperUrl:"whitePaperUrl",price:"hartDiskServerPrice"}},
];
class QueryFormPopup extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            stateUp:false,
            orderInfo:this.props.orderInfo,
            addSuccess:false
        };
    }
   async componentDidMount() {
       console.log("456");
       this.props.form.resetFields();
    }
    //下载白皮书
    downloadWhitePaper=async(url)=>{
        try {
            const result= await downloadWhitePaper({
                filename:url.split("/").pop().replace(/(^files\\)/g,""),
                whitePaperUrl:url
            });
                if(result.errorCode){
            }else{
                    let link = document.createElement("a");
                    link.href = window.URL.createObjectURL(result);
                    link.download=url.split("/").pop().replace(/(^files\\)/g,"");
                    link.click();
                    window.URL.revokeObjectURL(link.href);
                    console.log(link.href);
                }
        }
        catch (e) {
            message.error("下载白皮书失败")
        }
    };
    //存储订单
    addAOrder= async(fieldsValue)=>{
        try {
            let params=this.props.tabId==="TRUSTEESHIP"?{
                userRealityProportion:`${this.props.orderInfo.ordersEntity.userRealityProportion}`,
                userQueueProportion:`${this.props.orderInfo.ordersEntity.userQueueProportion}`,
                userStorage:`${this.props.orderInfo.ordersEntity.userStorage}`,
                bandwidthPrice:`${this.props.orderInfo.ordersEntity.bandwidthPrice}`,
                opsPriceCoefficient:`${this.props.fieldsValue.opsPrice}`,
                userNumber:`${this.props.orderInfo.ordersEntity.userNumber}`,
                scheduleClassesEverydayNumber:`${this.props.orderInfo.ordersEntity.scheduleClassesEverydayNumber}`,
                schooldaysPerWeekNumber:`${this.props.orderInfo.ordersEntity.schooldaysPerWeekNumber}`,
                systemUsersTotal:`${this.props.orderInfo.ordersEntity.systemUsersTotal}`,
                serviceCycle:`${this.props.orderInfo.ordersEntity.serviceCycle}`,
                hardDiskId:`${this.props.orderInfo.hardDiskEntity.id}`,
                cpuServerNumber:`${this.props.orderInfo.ordersEntity.cpuServerNumber}`,
                gpuId:`${this.props.orderInfo.gpuServer.id}`,
                storageId:`${this.props.orderInfo.storageServer.id}`,
                cpuId:`${this.props.orderInfo.cpuServer.id}`,
                ibId:`${this.props.orderInfo.ibServer.id}`,
                name:fieldsValue.name,
                remarks:fieldsValue.remarks,
                rackLeasedPrice:`${this.props.orderInfo.ordersEntity.rackLeasedPrice}`
            }:{
                userRealityProportion:`${this.props.orderInfo.ordersEntity.userRealityProportion}`,
                userQueueProportion:`${this.props.orderInfo.ordersEntity.userQueueProportion}`,
                userStorage:`${this.props.orderInfo.ordersEntity.userStorage}`,
                bandwidthPrice:`${this.props.orderInfo.ordersEntity.bandwidthPrice}`,
                opsPriceCoefficient:`${this.props.fieldsValue.opsPrice}`,
                userNumber:`${this.props.orderInfo.ordersEntity.userNumber}`,
                scheduleClassesEverydayNumber:`${this.props.orderInfo.ordersEntity.scheduleClassesEverydayNumber}`,
                schooldaysPerWeekNumber:`${this.props.orderInfo.ordersEntity.schooldaysPerWeekNumber}`,
                systemUsersTotal:`${this.props.orderInfo.ordersEntity.systemUsersTotal}`,
                serviceCycle:`${this.props.orderInfo.ordersEntity.serviceCycle}`,
                hardDiskId:`${this.props.orderInfo.hardDiskEntity.id}`,
                cpuServerNumber:`${this.props.orderInfo.ordersEntity.cpuServerNumber}`,
                gpuId:`${this.props.orderInfo.gpuServer.id}`,
                storageId:`${this.props.orderInfo.storageServer.id}`,
                cpuId:`${this.props.orderInfo.cpuServer.id}`,
                ibId:`${this.props.orderInfo.ibServer.id}`,
                name:fieldsValue.name,
                remarks:fieldsValue.remarks
            };
          const  result = await addAOrder(params);
            if(result.errorCode===1000){
                message.success("订单添加成功");
                this.setState({
                    addSuccess:true
                })
            }
            else{
                notification.info({
                    message: "存储订单失败"
                })
            }
        }
        catch (e) {
            notification.info({
                message: "存储订单失败"
            })
        }
    };
    queryFormClickOk=()=>{
        if(this.props.addQueryForm){
            this.props.form.validateFieldsAndScroll(async (err, fieldsValue) => {
                if (!err) {
                    console.log(fieldsValue);
                   await this.addAOrder(fieldsValue);
                    let newState = {
                        queryFormModalVisible: false,
                        addSuccess:this.state.addSuccess
                    };
                    this.props.modalChange(newState);
                }
            })
        }
        else {
            let newState = {
                queryFormModalVisible: false
            };
            this.props.modalChange(newState);
        }
        hardWaretotalPrice=0;
    };
    queryFormClickCancel=()=>{
        let newState={
            queryFormModalVisible:false
        };
        this.props.modalChange(newState);
        hardWaretotalPrice=0;
    };
    render() {
        hardWaretotalPrice=0;
        console.log(this.props.queryFormRecord);
        console.log( this.state.hardwareTopologyUrl);
        const titleStyle={width:"100%",height:"46px",padding:"10px 20px",background:"#F0F2F5"};
        const totalCount={fontSize:"16px",color:"#333333",marginTop:"14px",display:"inline-block"};
        const {getFieldDecorator} = this.props.form;
        const formItemLayout = {
            labelCol: {xs: { span: 2}, sm: { span: 2},},
            wrapperCol: { xs: { span: 9 }, sm: { span: 9 },},
        };
        return (
            <div>{
                this.props.queryFormModalVisible&&this.props.orderInfo?(
                    <Modal
                    title="询单详情"
                    visible={this.props.queryFormModalVisible}
                    onOk={this.queryFormClickOk}
                    okText="确认"
                    cancelText="取消"
                    onCancel={this.queryFormClickCancel}
                    width={1212}
                >
                    <div style={{marginBottom:"60px"}}>

                        <Row type="flex" style={!this.props.addQueryForm?{height:"73px",border:"0px"}:{display:"none"}}>
                            <Col span={4} offset={10} style={{textAlign:"center"}}>
                                <p style={{color:"#333333",fontSize:"18px",marginBottom:"0.6em"}}>{this.props.orderInfo.ordersEntity.name}</p>
                                <p style={{color:"#666666"}}>{this.props.orderInfo.ordersEntity.businessmanByBusinessmanId.name}&nbsp;{this.props.orderInfo.ordersEntity.registertime}</p>
                            </Col>
                        </Row>
                        <Row type="flex" style={{height:"208px"}}>
                            <div style={titleStyle}><h3 >用户需求</h3></div>
                            <div style={{padding:"0 20px",width:"100%"}}>
                                <div style={{height:"80px",paddingTop:"14px"}}>
                                    <h3>基本需求</h3>
                                    <span style={{marginRight:"60px"}}>同时上课人数: {this.props.orderInfo.ordersEntity.userNumber}人</span>
                                    <span style={{marginRight:"60px"}}>每天排课节数: {this.props.orderInfo.ordersEntity.scheduleClassesEverydayNumber}节</span>
                                    <span style={{marginRight:"60px"}}>每周上课天数: {this.props.orderInfo.ordersEntity.schooldaysPerWeekNumber}天</span>
                                    <span style={{marginRight:"60px"}}>总账号需求: {this.props.orderInfo.ordersEntity.systemUsersTotal}人</span>
                                    <span style={{marginRight:"60px"}}>服务年限: {this.props.orderInfo.ordersEntity.serviceCycle}年</span>
                                </div>
                                <Divider dashed={true} style={{borderColor:"#BBBBBB",margin:"0"}}/>
                                <div style={{height:"80px",paddingTop:"14px"}}>
                                    <h3>网络需求</h3>
                                    <span style={{marginRight:"60px"}}>每用户: {this.props.orderInfo.everyUserBandwidthMaximum}Mbps</span>
                                    <span style={{marginRight:"60px"}}>总计: {this.props.orderInfo.ordersEntity.bandwidthRequirements}Mbps</span>
                                </div>
                            </div>
                        </Row>
                        <Row type="flex" style={{marginBottom:"21px",border:"0px",height:"454px"}}>
                            <div style={{width:"740px",marginRight:"23px",height:"454px",border:"1px solid #BBBBBB"}}>
                                <div style={titleStyle}><h3 >硬件配置及架构</h3></div>
                                <div style={{padding:"0 20px",width:"100%"}}>
                                    {equipmentName.map((item,index)=>{return(
                                        <div style={{height:"66px",marginTop:"14px"}} key={item.name}>
                                            <h3>{item.name}</h3>
                                            <div style={{marginBottom:"14px"}}>
                                                <span style={{marginRight:"60px"}}>型号: {this.props.orderInfo[item.id]!==null?this.props.orderInfo[item.id][item.info.name]:"null"}</span>
                                                <span style={{marginRight:"60px"}}>数量: {this.props.orderInfo.ordersEntity[item.info.number]}{item.name==="硬盘"?"块":"台"}</span>
                                                <span style={item.name==="GPU服务器"?{marginRight:"60px"}:{display:"none"}}>卡数: {this.props.orderInfo.ordersEntity[item.info.Capacity]}卡</span>
                                                <span style={item.name==="存储服务器"?{marginRight:"60px"}:{display:"none"}}>存储量: {this.props.orderInfo.ordersEntity[item.info.Size]}TB</span>
                                                <span style={item.name==="存储服务器"?{marginRight:"60px"}:{display:"none"}}>硬盘数:  {this.props.orderInfo.ordersEntity[item.info.hardDiskNumber]}块</span>
                                                {this.props.orderInfo[item.id]!==null&&this.props.orderInfo[item.id][item.info.whitePaperUrl]!==null&&item.name!=="硬盘" && window.localStorage.getItem("role")==="ROLE_BUSINESSMAN" ?
                                                    (<a  onClick={()=>this.downloadWhitePaper(this.props.orderInfo[item.id][item.info.whitePaperUrl])}>下载白皮书</a>):""  }
                                            </div>

                                            { index===(equipmentName.length-1)?"":(<Divider dashed={true} style={{borderColor:"#BBBBBB",margin:"0"}}/>)}
                                        </div>
                                    )})}
                                </div>
                            </div>
                            <div style={{width:"401px",height:"454px", border:"1px solid #BBBBBB"}}>
                                <div style={titleStyle}><h3 >拓扑图</h3></div>
                                <div style={{padding:"14px 20px",width:"410px",height:"375px"}}>
                                    <img src={this.props.hardwareTopologyUrl} style={{width:"370px",height:"347px",marginBottom:"14px",display:"inline-block",overflow:"hidden"}} alt="图片加载失败"/>
                                    <a href={this.props.hardwareTopologyUrl} download={Date.parse(new Date())+".png"}>下载拓扑图</a>
                                </div>
                            </div>
                        </Row>
                        <Row type="flex">
                            <div style={titleStyle}><h3 >硬件成本</h3></div>
                            <div style={{padding:"0 20px",width:"100%"}}>
                                <div style={{height:"49px",paddingTop:"14px"}}>
                                    {

                                        equipmentName.map((item,index)=>{
                                        hardWaretotalPrice+=this.props.orderInfo.ordersEntity[item.info.price]*this.props.orderInfo.ordersEntity[item.info.number];
                                        return(
                                    <span key={index} style={{marginRight:"60px"}}>{item.name}: {`${this.props.orderInfo.ordersEntity[item.info.price]*this.props.orderInfo.ordersEntity[item.info.number]}`}元</span>
                                    )

                                    })}
                                </div>
                                <Divider dashed={true} style={{borderColor:"#BBBBBB",margin:"0"}}/>
                                <div style={{height:"49px"}}>
                                    <span style={totalCount}>总计: {hardWaretotalPrice}元</span>
                                </div>
                            </div>
                        </Row>
                        <Row type="flex">
                            <div style={titleStyle}><h3 >软件报价</h3></div>
                            <div style={{padding:"0 20px",width:"100%"}}>
                                <div style={{height:"49px",paddingTop:"14px"}}>
                                    <span style={{marginRight:"60px"}}>权限授权费: {this.props.orderInfo.ordersEntity.softwareLicensingPrice}元</span>
                                    <span style={{marginRight:"60px"}}>拓展课程包: 0元</span>
                                </div>
                                <Divider dashed={true} style={{borderColor:"#BBBBBB",margin:"0"}}/>
                                <div style={{height:"49px"}}>
                                    <span style={totalCount}>总计: {`${this.props.orderInfo.ordersEntity.softwareLicensingPrice+0}`}元</span>
                                </div>
                            </div>
                        </Row>
                        <Row type="flex">
                            <div style={titleStyle}><h3>其他成本</h3></div>
                            <div style={{padding:"0 20px",width:"100%"}}>
                                <div style={{height:"49px",paddingTop:"14px"}}>
                                    <span style={{marginRight:"60px"}}>网络: {this.props.orderInfo.ordersEntity.networkTotalPrice}元</span>
                                    <span style={{marginRight:"60px"}}>运维价格: {this.props.orderInfo.ordersEntity.opsTotalPrice}元</span>
                                    <span style={this.props.orderInfo.ordersEntity.rackLeasedPrice?{marginRight:"60px"}:{display:"none"}}>机柜租赁: {this.props.orderInfo.ordersEntity.rackLeasedPrice}元</span>

                                </div>
                                <Divider dashed={true} style={{borderColor:"#BBBBBB",margin:"0"}}/>
                                <div style={{height:"49px"}}>
                                    <span style={totalCount}>总计: {this.props.orderInfo.ordersEntity.rackLeasedPrice?`${this.props.orderInfo.ordersEntity.networkTotalPrice+this.props.orderInfo.ordersEntity.opsTotalPrice+this.props.orderInfo.ordersEntity.rackLeasedPrice}`:`${this.props.orderInfo.ordersEntity.networkTotalPrice+this.props.orderInfo.ordersEntity.opsTotalPrice}`}元</span>
                                </div>
                            </div>
                        </Row>
                        <div style={{margin:"10px 20px 30px"}}>
                            <span style={{fontSize:"26px"}}>服务周期内总价格: </span>
                            <span style={{fontSize:"26px",color:"#719CFB"}}>{this.props.orderInfo.ordersEntity.serviceCycleTotalPrice}元</span>
                        </div>
                        <Divider dashed={true} style={this.props.addQueryForm?{borderColor:"#BBBBBB"}:{display:"none"}}/>
                        <Form style={this.props.addQueryForm?{display:"block"}:{display:"none"}}>
                            <FormItem {...formItemLayout} label="询单名">
                                {getFieldDecorator('name',{
                                    rules: [{ required: true, message: "请输入询单名" }]
                                })(
                                    <Input  style={{color:"#999999"}} size="large"/>
                                )}
                            </FormItem>
                            <FormItem {...formItemLayout} label="添加备注">
                                {getFieldDecorator('remarks', {
                                })(
                                    <TextArea   style={{resize:"none",height:"152px"}}  />
                                )}
                            </FormItem>
                        </Form>
                    </div>
                </Modal>):""
            }</div>

        )
    }
}

export default Form.create()(QueryFormPopup)