import request from '../utils/request';
//获取销售人员列表
export function getBusinessmanList({page,size,...params}) {
    return request(`/api/v1/admin/businessman/getBusinessmanList`, {
        method: 'GET',
        headers:{
           ...params
        },
        body:{
            page,
            size
        }
    });
}
