import request from '../utils/request';
//用户查看自己拥有的订单列表
export function getOrderList(params) {
    return request('/api/v1/businessman/order/getOrderList', {
        method: 'GET',
        body: {
            ...params,
        },
    });
}
//用户根据订单名和备注查询订单列表
export function getOrderListLikeNameOrRemarks(params) {
    return request('/api/v1/businessman/order/getOrderListLikeNameOrRemarks', {
        method: 'GET',
        body: {
            ...params,
        },
    });
}
//删除一个订单
export function deleteAOrder(params) {
    return request('/api/v1/order/deleteAOrder', {
        method: 'POST',
        body: {
            ...params,
        },
    });
}
