//生成一个订单
export function generateAOrder(params) {
    return request('/api/v1/order/generateAOrder', {
        method: 'POST',
        body: params
    });
}
//存储一个订单，生成快照
export function addAOrder(params) {
    return request('/api/v1/order/addAOrder', {
        method: 'POST',
        body:params
    });
}
//询单下载
export function download(params) {
    return request('/api/v1/businessman/order/download', {
        method: 'POST',
        responseType: 'blob',
        body:params
    });
}
//商务人员下载服务器白皮书
export function downloadWhitePaper({...params}) {
    return request('/api/v1/businessman/download', {
        method: 'GET',
        responseType: 'blob',
        body:{...params}
    });
}





